.text {

  &__header {
    letter-spacing: 0.05px;
    text-transform: uppercase;
    font-size: .65rem;
    line-height: 125%;
    display: block;
  }

  &__title {
    padding-top: 1rem;
  }

  &__description {
    padding-top: 1.7rem;
    letter-spacing: 0.02px;
    color: $grey;
    font-size: .9rem;
    line-height: 166%;
  }

  &__plan-wrapper {
    padding-top: 1.2rem;
    max-width: 5.4rem;
    display: inline-block;
    font-weight: 700;
    text-align: center;
    position: relative;
  }

  &__plan-wrapper > hr {
    position: absolute;
    width: 100%;
    height: .1rem;
    background-color: $text-color;
    border: none;
  }

  &__plan {
    line-height: 100%;
    font-size: .9rem;
    letter-spacing: 0.02px;
    display: block;
  }

  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 3rem;
    text-align: center;
    letter-spacing: 0.02px;
    font-weight: 700;
    font-size: .65rem;
    line-height: 161%;
    color: $white;
    background-color: $text-color-lite;
    border: 1px solid #43806C;
    border-radius: 6px;
    width: 6.35rem;
    height: 2.35rem;
  }

  &__link:hover {
    background-color: $text-color;
  }
}

@media (max-width: $smDesktopWidth) {
  .text {

    &__header {
      font-size: .6rem;
    }

    &__description {
      font-size: .7rem;
      padding-top: 1rem;
    }

    &__plan-wrapper {
      padding-top: .5rem;
    }

    &__plan {
      font-size: .7rem;
    }
  }
}

@media (max-width: $smTableWidth) {
  .text {

    &__link {
      width: 5rem;
      height: 2rem;
    }
  }
}